		return {
			myListUrl:[
				'/pages/my/mygrowth',
				'/pages/my/myedu',
				'/pages/my/myInfomation',
				'/pages/my/myprefer',
				'/pages/my/myshare',
				'/pages/my/coupon/mycoupon',
				'/pages/my/mydeclear',
				'/pages/my/setting/setting',
				
			]
		};
	},
	methods: {
