				url:path
			})
		},
		handleCradit(){ //信用
			uni.navigateTo({
				url:'mycradit'
			})
		},
		handleEarn(){ //收益
			uni.navigateTo({
				url:'myearn'
			})
		},
		handleCertify(){ //认证
			uni.navigateTo({
				url:'../pub/certificate'
			})
		},
		handleMypub(){ //发布动态
			uni.navigateTo({
				url:'./mypub/mypub'
			})
		},
	},
}
